import React from 'react'
import {Drawer,Box, Typography, IconButton, Paper } from "@mui/material/"
import { useState } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import ListItemButton from '@mui/material/ListItemButton';
const FlacDrawer1 = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
    return (
        <>
        <Paper sx={{paddingLeft:"10px",
                     fontSize: "1.5rem",
                     fontWeight: 900,
                     boxShadow:"none",
                     letterSpacing: "0.15rem",
                     display:"flex",
                     alignItems:"center",
                     height:"3rem",
                     width:'16rem',
                     justifyContent:"space-between",
                    borderRadius:"0.75rem"}}>
        <Typography sx={{marginLeft:"10px",letterSpacing:"0.1rem" , fontWeight:"900"}}>
            FLAC
        </Typography>
        <IconButton size='large' color='inherit' onClick={() => setIsDrawerOpen(true)}>
            <MenuIcon  />
        </IconButton>
        </Paper>
    <Drawer anchor="right" open={isDrawerOpen} onClose={() => setIsDrawerOpen(false)}>
        <Box width="250px" textAlign="center" >
        <Typography variant='h6' component='div'></Typography>
        <List>
          {['Home','About', 'Features', 'Testimonials', 'Pricing'].map((text, index) => (
            <ListItem key={text} disablePadding>
              <ListItemButton>
                <ListItemText primary={text} />
              </ListItemButton>
            </ListItem>
          ))}
        </List>
        </Box>
    </Drawer>
    </>
  )
}
export default FlacDrawer1